import { Injectable, InjectionToken, Inject } from '@angular/core';

import { LoggerService, LogLevel } from './logger.service';

export const MULTI_LOGGER_SERVICES = new InjectionToken<LoggerService[]>(
  'multi-logger logger services'
);

/**
 * @requires MULTI_LOGGER_SERVICES
 */
@Injectable({
  providedIn: 'root',
})
export class MultiLoggerService extends LoggerService {

  public constructor(@Inject(MULTI_LOGGER_SERVICES) private loggerServices: LoggerService[]) {
    super();
  }

  public trace(message: string): void {
    for (const logger of this.loggerServices) {
      logger.trace(message);
    }
  }

  public debug(message: string): void {
    for (const logger of this.loggerServices) {
      logger.debug(message);
    }
  }

  public info(message: string): void {
    for (const logger of this.loggerServices) {
      logger.info(message);
    }
  }

  public warning(message: string): void {
    for (const logger of this.loggerServices) {
      logger.warning(message);
    }
  }

  public error(message: string): void {
    for (const logger of this.loggerServices) {
      logger.error(message);
    }
  }

  public fatal(message: string): void {
    for (const logger of this.loggerServices) {
      logger.fatal(message);
    }
  }

  public log(level: LogLevel, message: string) {
    for (const logger of this.loggerServices) {
      logger.log(level, message);
    }
  }
}
