import { FormControl, AbstractControl } from '@angular/forms';

// custom validator to check that two fields match
export function mustMatch(matchingControl: FormControl) {
    return (control: AbstractControl) => {
        // return null if controls haven't initialised yet
        if (!control || !matchingControl) {
            return null;
        }

        // return null if another validator has already found an error on the matchingControl
        if (control.errors && !control.errors.mustMatch) {
            return null;
        }

        // set error on matchingControl if validation fails
        if (control.value !== matchingControl.value) {
            control.setErrors({ mustMatch: true });
        } else {
            matchingControl.setErrors(null);
        }
    };
}

export function getMustMatchError(control: FormControl, controlName: string, matchControlName: string): string | null {
    if (control.errors && control.errors.mustMatch) {
        return `${controlName} must match with ${matchControlName}.`;
    }
    return null;
}
