import { FormControl } from '@angular/forms';

export function getRequiredError(control: FormControl): string | null {
    if (control.errors && control.errors.required) {
        return `Required.`;
    }
    return null;
}

export function getEmailError(control: FormControl): string | null {
    if (control.errors && control.errors.email) {
        return `No valid email.`;
    }
    return null;
}
