import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';

import { HttpJsonToObjectService } from './http-json-to-object.service';
import { ModelError } from '../../errors/model-error';
import { Identity } from '../../models/identity';
import { DataListQueryHelper, DataListQueryHelperConfig } from './data-list-query-helper';
import { LoggerService } from '../log/logger.service';


export abstract class CrudService<TId extends string | number, T extends Identity<TId>> extends HttpJsonToObjectService {

  constructor(
    protected httpClient: HttpClient,
    protected type: new() => T,
    protected logger: LoggerService,
    startsUrl: string = '',
    dataListQueryHelperConfig: DataListQueryHelperConfig = DataListQueryHelper.defaultConfig
  ) {
    super(httpClient, logger, startsUrl, dataListQueryHelperConfig);
  }

  /**
   * Create a new item
   */
  public create(item: T): Observable<T> {
    if (!item) {
      return throwError(new ModelError('created user group mustn\'t be null.'));
    }

    return this.postAndReturnObject('', item);
  }

  /**
   * Get one item
   */
  public read(id: TId): Observable<T> {
    if (!id) {
      return throwError(new ReferenceError('null reference user group id'));
    }

    return this.getJson<T>(`/${id}`, this.type);
  }

  /**
   * Get all items
   */
  public readAll(): Observable<T[]> {
    return this.getJsonArray<T>('', this.type);
  }

  /**
   * Update an item
   */
  public update(item: T): Observable<void> {
    if (!(item && item.id)) {
      return throwError(new ModelError('updated user group mustn\'t be null and must have an id.'));
    }

    return this.put<T>(`/${item.id}`, item);
  }

  /**
   * Delete an item
   */
  public delete(idOrItem: TId | T): Observable<void> {
    idOrItem = typeof idOrItem === 'object' ? idOrItem.id : idOrItem;
    if (!idOrItem) {
      return throwError(new ReferenceError('null reference user group id'));
    }

    return this.deleteFromRoute(`/${idOrItem}`);
  }
}
