import { HttpParams } from '@angular/common/http';
import { TablePaginationParams, TableParams } from './http-json-to-object.service';

export interface DataListQueryHelperConfig {
  pageSizeKey: string;
  pageNbKey: string;
  sortKey: string;
  orderKey: string;
}

export class DataListQueryFilter {
  constructor(public field: string, public value: string) {}
}

export class DataListQueryHelper {
  public static defaultConfig = {
    pageSizeKey: '_limit',
    pageNbKey: '_page',
    sortKey: '_sort',
    orderKey: '_order'
  };

  constructor(private config: DataListQueryHelperConfig = DataListQueryHelper.defaultConfig) {}

  public getTableRequestParameters(tableParams?: TableParams): HttpParams {
    let params = new HttpParams();
    if (tableParams) {
      params = tableParams.sort ? params.append(this.config.sortKey, tableParams.sort) : params;
      params = tableParams.orderDesc ? params.append(this.config.orderKey, tableParams.orderDesc.toString()) : params;
      for (const filter of tableParams.filters || []) {
        params = params.append(filter.field, filter.value);
      }
    }
    return params;
  }

  public getTablePaginationRequestParameters(tableParams: TablePaginationParams): HttpParams {
    let params = new HttpParams();
    params = tableParams.sort ? params.append(this.config.sortKey, tableParams.sort) : params;
    params = tableParams.orderDesc ? params.append(this.config.orderKey, tableParams.orderDesc.toString()) : params;
    params = tableParams.currentPage ? params.append(this.config.pageNbKey, tableParams.currentPage.toString()) : params;
    params = tableParams.pageSize ? params.append(this.config.pageSizeKey, tableParams.pageSize.toString()) : params;
    for (const filter of tableParams.filters || []) {
      params = params.append(filter.field, filter.value);
    }
    return params;
  }
}
