import { HttpClient, HttpParams, HttpHeaders, HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { JsonToTypedHelper } from '../../utils/json-to-typed-helper';
import { catchError, map, mapTo } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';
import { ResponseHeadersRetrieverHelper } from '../response-headers-retriever';
import { DataListQueryHelper, DataListQueryFilter, DataListQueryHelperConfig } from './data-list-query-helper';
import { isNullOrUndefined } from 'util';
import { LoggerService } from '../log/logger.service';

export abstract class HttpJsonToObjectService {
  protected dataListQueryHelper: DataListQueryHelper;

  constructor(
    protected http: HttpClient,
    protected logger: LoggerService,
    protected startsUrl = '',
    dataListQueryHelperConfig: DataListQueryHelperConfig = DataListQueryHelper.defaultConfig
  ) {
    this.dataListQueryHelper = new DataListQueryHelper(dataListQueryHelperConfig);
  }

  protected get(route: string, options?: BasicOptions) {
    return this.http.get(this.startsUrl + route, options).pipe(catchError(this.handleError()));
  }

  protected getArrayBuffer(route: string, options?: BasicOptions): Observable<ArrayBuffer> {
    return this.http
      .get(this.startsUrl + route, Object.assign({}, options, { responseType: 'arraybuffer' }))
      .pipe(catchError(this.handleError())) as Observable<ArrayBuffer>;
  }

  protected getBlob(route: string, options?: BasicOptions): Observable<Blob> {
    return this.http
      .get(this.startsUrl + route, Object.assign({}, options, { responseType: 'blob' }))
      .pipe(catchError(this.handleError())) as Observable<Blob>;
  }

  protected post<T>(route: string, body: T, options?: BasicOptions) {
    return this.http.post(this.startsUrl + route, body, options).pipe(catchError(this.handleError()));
  }

  protected postAndReturnId<TBody, TId>(route: string, body: TBody, options?: BasicOptions) {
    return this.http.post(this.startsUrl + route, JsonToTypedHelper.serialize(body), options).pipe(
      map(x => x as TId),
      catchError(this.handleError())
    );
  }

  protected postAndReturnObject<T>(route: string, body: T, options?: BasicOptions) {
    // TODO QNS : Map avec un deserialize
    return this.http.post<T>(this.startsUrl + route, JsonToTypedHelper.serialize(body), options).pipe(catchError(this.handleError()));
  }

  protected postAndReturnJson<T>(route: string, body: T, type: new () => T, options?: BasicOptions) {
    return this.post(route, body, options).pipe(
      map(r => JsonToTypedHelper.deserialize(r, type)),
      catchError(this.handleError())
    );
  }

  protected put<T>(route: string, body: T, options?: BasicOptions): Observable<void> {
    return this.http.put(this.startsUrl + route, JsonToTypedHelper.serialize(body), options).pipe(
      mapTo(void 0),
      catchError(this.handleError())
    );
  }

  protected putAndReturnObject<T>(route: string, body: T, options?: BasicOptions): Observable<T> {
    return this.http.put(this.startsUrl + route, JsonToTypedHelper.serialize(body), options).pipe(
      map(x => x as T),
      catchError(this.handleError())
    );
  }

  protected patch<T>(route: string, body: T, options?: BasicOptions) {
    return this.http.patch(this.startsUrl + route, JsonToTypedHelper.serialize(body), options).pipe(catchError(this.handleError()));
  }

  protected deleteFromRoute(route: string, options?: BasicOptions): Observable<void> {
    return this.http.delete(this.startsUrl + route, options).pipe(
      mapTo(void 0),
      catchError(this.handleError())
    );
  }

  protected deleteAndReturnObject<T>(route: string, options?: BasicOptions): Observable<T> {
    return this.http.delete(this.startsUrl + route, options).pipe(
      map(x => x as T),
      catchError(this.handleError())
    );
  }

  // protected postJson<T>(
  //     route: string,
  //     value: T,
  //     type: new () => T,
  //     options?: BasicOptions
  // ): Observable<T> {
  //     return this.http.post<T>(this.startsUrl + route, , options)
  // }

  protected getJson<T>(route: string, type: new () => T, options?: BasicOptions): Observable<T> {
    return this.http.get(this.startsUrl + route, options).pipe(
      map(res => {
        const obj = JsonToTypedHelper.deserialize(res, type);
        return obj;
      }),
      catchError(this.handleError())
    );
  }

  protected getJsonArray<T>(route: string, type: new () => T, options?: BasicOptions, tableParams?: TableParams): Observable<T[]> {
    return this.http.get(this.startsUrl + route, { params: this.getMergedHttpParams(options, tableParams) }).pipe(
      map(res => JsonToTypedHelper.deserializeArray(res as object[], type)),
      catchError(this.handleError())
    );
  }

  protected getJsonArrayPagination<T extends object>(
    paginationParams: TablePaginationParams,
    route: string,
    type: new () => T,
    options?: BasicOptions
  ): Observable<PaginationCollection<T>> {
    return this.getArrayBodyWithHeaders(
      route,
      [TablePaginationParams.httpPaginationHeader],
      type,
      Object.assign({ params: this.dataListQueryHelper.getTablePaginationRequestParameters(paginationParams) }, options)
    ).pipe(
      map(res => {
        return {
          pagination: res.headers.get(TablePaginationParams.httpPaginationHeader) as PaginationHeader,
          collection: res.body as T[]
        };
      }),
      catchError(this.handleError())
    );
  }

  /**
   * Get a response with an extraction of the header and the body of the type T
   * @param route http route
   * @param headerKeys keys to extract inside the http response header
   * @param bodyType type of the http response body
   * @param options http options
   */
  protected getJsonBodyWithHeaders<T extends object>(
    route: string,
    headerKeys: string[],
    bodyType?: new () => T,
    options?: BasicOptions
  ): Observable<BodyWithHeaders<T>> {
    return this.http.get<HttpResponse<object>>(this.startsUrl + route, Object.assign(options ? options : {}, { observe: 'response' })).pipe(
      map(res => this.extractBodyWithHeaders(res, headerKeys, bodyType)),
      catchError(this.handleError())
    );
  }

  /**
   * Get a response with an extraction of the header and the body raw
   * @param route http route
   * @param headerKeys keys to extract inside the http response header
   * @param options http options
   */
  protected getRawBodyWithHeaders(route: string, headerKeys: string[], options?: BasicOptions): Observable<BodyWithHeaders<any>> {
    return this.http.get<HttpResponse<any>>(this.startsUrl + route, Object.assign(options ? options : {}, { observe: 'response' })).pipe(
      map(res => this.extractBodyWithHeaders(res, headerKeys)),
      catchError(this.handleError())
    );
  }

  /**
   * Get a response with an extraction of the header and the body of the type T
   * @param route http route
   * @param headerKeys keys to extract inside the http response header
   * @param bodyType type of the http response body
   * @param options http options
   */
  protected getArrayBodyWithHeaders<T extends object>(
    route: string,
    headerKeys: string[],
    bodyType: new () => T,
    options?: BasicOptions
  ): Observable<ArrayBodyWithHeaders<T>> {
    return this.http.get<HttpResponse<object>>(this.startsUrl + route, Object.assign(options ? options : {}, { observe: 'response' })).pipe(
      map(res => this.extractArrayBodyWithHeaders(res, headerKeys, bodyType)),
      catchError(this.handleError())
    );
  }

  protected extractHeaders(response: HttpResponse<any>, headerKeys: string[]): Map<string, object> {
    const headers = new Map<string, object>();
    for (const key of headerKeys) {
      headers.set(key, ResponseHeadersRetrieverHelper.getJsonHeaderValue(response, key));
    }
    return headers;
  }

  protected extractBodyWithHeaders<T extends object>(
    response: HttpResponse<any>,
    headerKeys: string[],
    bodyType?: new () => T
  ): BodyWithHeaders<T> {
    const result = new BodyWithHeaders<T>();
    result.headers = this.extractHeaders(response, headerKeys);
    if (bodyType) {
      result.body = JsonToTypedHelper.deserialize(response.body, bodyType);
    } else {
      result.body = response.body;
    }
    return result;
  }

  protected extractArrayBodyWithHeaders<T extends object>(
    response: HttpResponse<any>,
    headerKeys: string[],
    bodyType: new () => T
  ): ArrayBodyWithHeaders<T> {
    const result = new ArrayBodyWithHeaders<T>();
    result.headers = this.extractHeaders(response, headerKeys);
    result.body = JsonToTypedHelper.deserializeArray(response.body as object[], bodyType);
    return result;
  }

  protected setHttpParams<T = any>(name: string, value: T | T[], httpParams?: HttpParams): HttpParams {
    if (isNullOrUndefined(httpParams)) {
      httpParams = new HttpParams();
    }
    if (!isNullOrUndefined(value)) {
      if (value instanceof Date) {
        return httpParams.set(name, value.toISOString());
      } else if (value instanceof Array) {
        if (value.length > 0 && value[0] instanceof Object) {
          return httpParams.set(name, JSON.stringify(JsonToTypedHelper.serializeArray<T>(value)));
        } else {
          return httpParams.set(name, JSON.stringify(value));
        }
      } else if (value instanceof Object) {
        return httpParams.set(name, JSON.stringify(JsonToTypedHelper.serialize<T>(value)));
      } else if (typeof value === 'string') {
        return httpParams.set(name, value);
      } else {
        return httpParams.set(name, JSON.stringify(value));
      }
    }
    return httpParams;
  }

  protected getMergedHttpParams(options?: BasicOptions, tableParams?: TableParams): HttpParams {
    let mergedHttpParams = this.dataListQueryHelper.getTableRequestParameters(tableParams);
    if (!isNullOrUndefined(options) && !isNullOrUndefined(options.params)) {
      if (options.params instanceof HttpParams) {
        for (const key of options.params.keys()) {
          for (const value of options.params.getAll(key)) {
            mergedHttpParams = mergedHttpParams.append(key, value);
          }
        }
      } else {
        for (const key of Object.keys(options.params)) {
          if (options.params[key] instanceof Array) {
            for (const value of options.params[key]) {
              mergedHttpParams = mergedHttpParams.append(key, value);
            }
          } else {
            mergedHttpParams = mergedHttpParams.append(key, options.params[key] as string);
          }
        }
      }
    }
    return mergedHttpParams;
  }

  protected handleError() {
    return (error: HttpErrorResponse) => {
      if (error.error instanceof ErrorEvent) {
        // A client-side or network error occurred. Handle it accordingly.
        this.logger.error(`An error occurred: ${error.error.message}`);
      } else if (error instanceof TypeError) {
        // A type conversion occur.
        this.logger.error(`Type convertion: ${error.message}`);
      } else {
        this.logger.error(error.message);
      }
      return throwError(error.name);
    };
  }
}

/**
 * options type for get/set/delete/put/post/patch
 */
export interface BasicOptions {
  headers?:
    | HttpHeaders
    | {
        [header: string]: string | string[];
      };
  observe?: 'body';
  params?:
    | HttpParams
    | {
        [param: string]: string | string[];
      };
  reportProgress?: boolean;
  responseType?: 'json';
  withCredentials?: boolean;
}

export class BodyWithHeaders<T extends object> {
  headers: Map<string, object>;
  body: T;
}

export class ArrayBodyWithHeaders<T extends object> {
  headers: Map<string, object>;
  body: T[];
}

export class TableParams {
  sort?: string;
  orderDesc?: boolean;
  filters?: DataListQueryFilter[];
}

export class TablePaginationParams extends TableParams {
  static readonly httpPaginationHeader = 'x-pagination';

  pageSize: number;
  currentPage: number;
}

export interface PaginationHeader {
  pageSize: number;
  currentPage: number;
  totalCount: number;
  totalPages: number;
}

export class PaginationCollection<T> {
  pagination: PaginationHeader;
  collection: T[];
}
